import {
  ADD_TODO,
  FETCH_TODOS,
  LOADER_CHANGE,
  FINISH_TODO,
  UNFINISH_TODO,
  UPDATE_TODOS,
  DEL_TODO
} from "./actions";
import { addTodo, fetchTodos, updateTodos } from "../api_db/api_db";

export const middleWare = state => next => action => {
  const inputValue = state.getState().todoReducer.inputValue;
  const todos = state.getState().todoReducer.todos;

  if (action.type === ADD_TODO) {
    if (inputValue.trim()) {
      next({ type: LOADER_CHANGE, payload: true });
      const newTodo = {
        title: inputValue,
        completed: false,
        date: Number(new Date())
      };

      return addTodo(newTodo)
        .then(() => {
          next({ ...action, payload: newTodo });
        })
        .catch(err => console.log(err))
        .finally(() => next({ type: LOADER_CHANGE, payload: false }));
    }
  } else if (action.type === FETCH_TODOS) {
    next({ type: LOADER_CHANGE, payload: true });
    return fetchTodos()
      .then(datas => next({ ...action, payload: datas }))
      .catch(err => console.log(err))
      .finally(() => next({ type: LOADER_CHANGE, payload: false }));
  } else if (action.type === FINISH_TODO) {
    next({ type: LOADER_CHANGE, payload: true });
    const newTodos = todos.map(todo => {
      return todo.date === action.payload.date
        ? { ...todo, completed: true }
        : todo;
    });

    return updateTodos(newTodos)
      .then(() => {
        return next({ type: UPDATE_TODOS, payload: newTodos });
      })
      .finally(() => next({ type: LOADER_CHANGE, payload: false }));
  } else if (action.type === UNFINISH_TODO) {
    next({ type: LOADER_CHANGE, payload: true });
    const newTodos = todos.map(todo => {
      return todo.date === action.payload.date
        ? { ...todo, completed: false }
        : todo;
    });

    return updateTodos(newTodos)
      .then(() => {
        return next({ type: UPDATE_TODOS, payload: newTodos });
      })
      .finally(() => next({ type: LOADER_CHANGE, payload: false }));
  } else if (action.type === DEL_TODO) {
    next({ type: LOADER_CHANGE, payload: true });
    const newTodos = todos.filter(todo => {
      return todo.date !== action.payload.date;
    });

    return updateTodos(newTodos)
      .then(() => {
        return next({ type: UPDATE_TODOS, payload: newTodos });
      })
      .finally(() => next({ type: LOADER_CHANGE, payload: false }));
  } else return next(action);
};
