import React from "react";
import { connect } from "react-redux";
import { INPUT_CHANGE, ADD_TODO } from "../redux/actions";

function Input({ inputValue, handleChange, onAdd }) {
  return (
    <input
      value={inputValue}
      onChange={handleChange}
      placeholder="Text todo"
      onKeyUp={onAdd}
    />
  );
}

function mapState(state) {
  return {
    inputValue: state.todoReducer.inputValue
  };
}

function mapDispatch(dispatch) {
  return {
    handleChange: ({ target }) => {
      dispatch({ type: INPUT_CHANGE, payload: target.value });
    },
    onAdd: ({ keyCode }) => (keyCode === 13 ? dispatch({ type: ADD_TODO }) : "")
  };
}

export default connect(
  mapState,
  mapDispatch
)(Input);
