import React from "react";
import { connect } from "react-redux";
import { FINISH_TODO, UNFINISH_TODO, DEL_TODO } from "../redux/actions";

function Todo({
  todo,
  finish,
  unfinish,
  del,
  todo: { title, date, completed }
}) {
  return (
    <div className="todo-wrap">
      <p className={`${completed ? "line-through" : ""} todo-title`}>{title}</p>
      <p className="todo-date">{new Date(date).toLocaleString()}</p>
      <div className="buttons">
        {completed ? (
          <button className="primary" onClick={() => unfinish(todo)}>
            Unfinish
          </button>
        ) : (
          <button className="primary" onClick={() => finish(todo)}>
            Finish
          </button>
        )}
        <button className="danger" onClick={() => del(todo)}>
          Delete
        </button>
      </div>
    </div>
  );
}

function mapDispatch(dispatch) {
  return {
    finish: todo => dispatch({ type: FINISH_TODO, payload: todo }),
    unfinish: todo => dispatch({ type: UNFINISH_TODO, payload: todo }),
    del: todo => dispatch({ type: DEL_TODO, payload: todo })
  };
}

export default connect(
  null,
  mapDispatch
)(Todo);
