import { INPUT_CHANGE, ADD_TODO, FETCH_TODOS, UPDATE_TODOS } from "../actions";

let initState = {
  inputValue: "",
  todos: [],
  filterValue: ""
};

export default (state = initState, action) => {
  switch (action.type) {
    case INPUT_CHANGE:
      return { ...state, inputValue: action.payload };
    case ADD_TODO:
      return {
        ...state,
        inputValue: "",
        todos: [action.payload, ...state.todos]
      };
    case FETCH_TODOS:
      const todos = [...state.todos, ...action.payload];
      const sortedTodos = todos.sort((a, b) => {
        return b.date - a.date;
      });
      return { ...state, todos: [...sortedTodos] };
    case UPDATE_TODOS:
      const newtodos = [...action.payload];
      return { ...state, todos: newtodos };
    default:
      return state;
  }
};
