firebase.initializeApp(config);
const databaseRef = firebase.database().ref();
const todosRef = databaseRef.child("redux-todo/");

/* Functions */
export function addTodo(todo) {
  return todosRef.child(todo.date).set(todo);
}

export function fetchTodos() {
  return new Promise((resolve, reject) => {
    todosRef.on("value", data => {
      let todosObj = data.val();
      let todos = [];
      if (todosObj) {
        todos = Object.values(todosObj);
        resolve(todos);
      } else resolve(todos);
    });
  });
}

export function updateTodos(todos) {
  let updates = {};

  todos.map(todo => {
    return (updates[todo.date] = todo);
  });

  return todosRef.set(updates);
}
